.textfield_subscription__label{
  position: relative;
}
.textfield_subscription__label:after{
  content: "";
  display: block;
  position: absolute;
  top: 20px;
  right: 2px;
  width: 44px;
  height: 44px;
  cursor: pointer;
  background: url("../../../blocks/textfield/textfield_subscription/svg/arrow_forward.svg") no-repeat 12px 12px;
}